<template>
  <div class='wrap'>
    <wv-icon type="success" :large="true" style="margin-top: 40px"></wv-icon>
    <h3 style="margin-top: 40px">反馈成功</h3>
    <p>{{$route.params.text}}</p>
    <p class="submit"><wv-button type="primary" @click="close">关闭</wv-button></p>
    <wv-footer class="footer" text="Copyright © 2018 xingnanzhuli"></wv-footer>
  </div>
</template>

<script>
import { Toast } from "we-vue";

export default {
  name: "Feedback",
  data() {
    return {};
  },
  methods: {
    close() {
      var userAgent = navigator.userAgent;
      if (typeof WeixinJSBridge != "undefined") {
        WeixinJSBridge.call("closeWindow");
      } else {
        if (userAgent.indexOf("MSIE") > 0) {
          if (userAgent.indexOf("MSIE 6.0") > 0) {
            window.opener = null;
            window.close();
          } else {
            window.open("", "_top");
            window.top.close();
          }
        } else if (
          userAgent.indexOf("Firefox") != -1 ||
          userAgent.indexOf("Chrome") != -1
        ) {
          window.location.href = "about:blank ";
        } else {
          window.opener = null;
          window.open("", "_self", "");
          window.close();
        }
      }
    }
  }
};
</script>

<style scoped>
.wrap {
  background-color: #f0eff5;
  width: 100vw;
  height: 100vh;
  text-align: center;
}
p {
  padding: 5px 25px;
  font-size: 14px;
  line-height: 17px;
  color: #a3a3a3;
}
.submit {
  padding: 20px;
}
.footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding-top: 40px;
  padding-bottom: 10px;
  text-align: center;
}
</style>



