<template>
  <div class='wrap'>
    <wv-group title="贡献话术" style="margin-top: 20px;">
      <wv-input label="美女" placeholder="请输入话术问题" v-model="huashu.femaleText"></wv-input>
      <wv-input label="型男" placeholder="请输入话术答案" v-model="huashu.maleText"></wv-input>
    </wv-group>
    <wv-group title="贡献惯例">
      <wv-textarea placeholder="请输入惯例" :show-counter="false" v-model="guanli"></wv-textarea>
    </wv-group>
    <wv-group title="贡献情话">
      <wv-textarea placeholder="请输入情话" :show-counter="false" v-model="qinghua"></wv-textarea>
    </wv-group>
    <wv-group title="贡献恋爱策略">
      <wv-textarea placeholder="请输入恋爱策略" :show-counter="false" v-model="celue"></wv-textarea>
    </wv-group>
    <div class="weui-gallery">
      <span class="weui-gallery__img" style="background-image: url(./images/pic_article.png);"></span>
      <div class="weui-gallery__opr">
          <a href="javascript:" class="weui-gallery__del">
              <i class="weui-icon-delete weui-icon_gallery-delete"></i>
          </a>
      </div>
    </div>
    <p class="weui-cells__title">贡献表情/图片</p>
    <div class="weui-cells weui-cells_form" id="uploaderCustom">
        <div class="weui-cell">
            <div class="weui-cell__bd">
                <div class="weui-uploader">
                    <div class="weui-uploader__hd">
                        <p class="weui-uploader__title">图片上传</p>
                        <div class="weui-uploader__info"><span id="uploadCount">0</span>/{{maxCount}}</div>
                    </div>
                    <div class="weui-uploader__bd">
                        <ul class="weui-uploader__files" id="uploaderCustomFiles"></ul>
                        <div class="weui-uploader__input-box">
                            <input id="uploaderCustomInput" class="weui-uploader__input" type="file" accept="image/*" multiple="multiple">
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <br/>
    <p class="submit"><wv-button id="uploaderCustomBtn" type="primary" @click="showSuccess">提交</wv-button></p>
    <br/>
    <wv-footer class="footer" text="Copyright © 2018 xingnanzhuli"></wv-footer>
  </div>
</template>

<script>
import axios from "axios";
import { Toast } from "we-vue";

export default {
  name: "Contribute",
  props: ["maxCount", "uuid", "images"], // 在App.vue中设置
  data() {
    return {
      text: "Welcome, this is the Contribute page!",
      huashu: { femaleText: "", maleText: "" },
      guanli: "",
      qinghua: "",
      celue: "",
      fileArr: []
    };
  },
  methods: {
    showSuccess() {
      if (
        (this.huashu.femaleText !== "" && this.huashu.maleText === "") ||
        (this.huashu.femaleText === "" && this.huashu.maleText !== "")
      ) {
        Toast.text({
          duration: 1000,
          message: "贡献话术部分请输入完整话术～"
        });
      } else if (
        this.huashu.femaleText === "" &&
        this.huashu.maleText === "" &&
        this.guanli === "" &&
        this.qinghua === "" &&
        this.celue === ""
      ) {
        Toast.text({
          duration: 1000,
          message: "至少贡献一条内容才能提交哦～"
        });
      } else {
        axios
          .post("/contribute", {
            params: {
              uuid: this.uuid,
              huashu: this.huashu,
              guanli: this.guanli,
              qinghua: this.qinghua,
              celue: this.celue,
              images: this.images
            }
          })
          .then(res => {
            let text = "恋爱联盟感谢您的贡献，关注与支持";
            this.$router.push({ name: "Reminder", params: { text } });
          });
      }
    }
  }
};
</script>

<style scoped>
.weui-cells {
  margin-top: 5px !important;
}
.weui-cell .weui-label {
  width: 52px !important;
}
</style>



