import Vue from "vue";
import Router from "vue-router";
import Contribute from "@/components/Contribute";
import Feedback from "@/components/Feedback";
import Reminder from "@/components/Reminder";

Vue.use(Router);

export default new Router({
    // mode: "history",
    routes: [
        {
            path: "/",
            redirect: '/contribute'
        },
        {
            path: "/contribute",
            name: "Contribute",
            component: Contribute
        },
        {
            path: "/feedback",
            name: "Feedback",
            component: Feedback
        },
        {
            path: "/reminder",
            name: "Reminder",
            component: Reminder
        }
    ]
});



