<template>
  <div id="app"><router-view :maxCount="maxCount" :uuid="uuid" :images="images"/></div>
</template>

<script>
import "weui";
import weui from "weui.js";

export default {
  name: "App",
  data() {
    return {
      maxCount: 20,
      uuid: "",
      images: []
    };
  },
  mounted() {
    // 生成 uuid
    var d = new Date().getTime();
    var uuid = "xxxxxxxxyyyyxxxx".replace(/[xy]/g, function(c) {
      var r = ((d + Math.random() * 16) % 16) | 0;
      d = Math.floor(d / 16);
      return (c == "x" ? r : (r & 0x3) | 0x8).toString(16);
    });
    this.uuid = uuid;
    /* 图片手动上传 */
    var uploadCustomFileList = [];
    var images = [];
    var uploadCount = 0;
    var uploadCountDom = document.getElementById("uploadCount");
    // 服务器地址
    var server = BASE_URL;
    // 允许上传的图片类型
    var allowTypes = ["image/jpg", "image/jpeg", "image/png", "image/gif"];
    // maxSixe * 1024 * 1024，也就是 10MB
    var maxSize = 10;
    // 图片最大宽度
    var maxWidth = 300;
    // 最大上传图片数量
    var maxCount = this.maxCount;
    // 这里是简单的调用，其余api请参考文档
    weui.uploader("#uploaderCustom", {
      url: server + "/addimage",
      auto: false,
      onQueued: function() {
        uploadCustomFileList.push(this);
        var uploadID = this.id;
        $("#uploaderFiles li").each(function() {
          if ($(this).attr("data-id") == uploadID) {
            $(this).attr("data-fid", ret.OtherData);
          }
        });
      },
      onBeforeQueued: function onBeforeQueued(files) {
        if (allowTypes.indexOf(this.type) < 0) {
          weui.alert("请上传图片");
          return false;
        }
        if (this.size > maxSize * 1024 * 1024) {
          weui.alert("".concat("请上传不超过", maxSize, "M的图片"));
          return false;
        }
        if (files.length > maxCount) {
          // 防止一下子选中过多文件
          weui.alert("".concat("最多只能上传", maxCount, "张图片，请重新选择"));
          return false;
        }
        if (uploadCount + 1 > maxCount) {
          weui.alert("".concat("最多只能上传", maxCount, "张图片"));
          return false;
        }
        ++uploadCount;
        uploadCountDom.innerHTML = uploadCount;
      },
      onBeforeSend: function onBeforeSend(data, headers) {
        $("#submitBtn").addClass("weui-btn_disabled");
        $("#loadingToast").fadeIn(100);
        // $.extend(data, { uuid: uuid }); // 可以扩展此对象来控制上传参数
        data.uuid = uuid;
        data.image_id = uploadCount;
        images.push(data);
        // console.log(this, data, headers);
        // $.extend(headers, { Origin: "http://localhost:8081/v1/api" }); // 可以扩展此对象来控制上传头部
        // return false; // 阻止文件上传
      },
      onProgress: function onProgress(procent) {
        // console.log(this, procent);
      },
      onSuccess: function onSuccess(ret) {
        if (ret.Result == true) {
          uploadSuccessCount++;
          if (uploadSuccessCount == uploadCount) {
            $("#loadingToast").fadeOut(100);
            $("#submitBtn").removeClass("weui-btn_disabled");
          }
        }
        // $("#uploaderFiles li").each(function() {
        //   if ($(this).attr("data-id") == uploadID) {
        //     $(this).attr("data-fid", ret.OtherData);
        //   }
        // });
        // console.log(this, ret);
      },
      onError: function onError(err) {
        console.log(this, err);
      }
    });

    // 手动上传按钮
    document
      .getElementById("uploaderCustomBtn")
      .addEventListener("click", function() {
        uploadCustomFileList.forEach(function(file) {
          file.upload();
        });
      });

    // 缩略图预览
    document
      .querySelector("#uploaderCustomFiles")
      .addEventListener("click", function(e) {
        var target = e.target;

        while (!target.classList.contains("weui-uploader__file") && target) {
          target = target.parentNode;
        }
        if (!target) return;

        var url = target.getAttribute("style") || "";
        var id = target.getAttribute("data-id");

        if (url) {
          url = url.match(/url\((.*?)\)/)[1].replace(/"/g, "");
        }
        var gallery = weui.gallery(url, {
          onDelete: function() {
            weui.confirm("确定删除该图片？", function() {
              var index;
              for (var i = 0, len = uploadCustomFileList.length; i < len; ++i) {
                var file = uploadCustomFileList[i];
                if (file.id == id) {
                  index = i;
                  break;
                }
              }
              if (index !== undefined) {
                uploadCustomFileList.splice(index, 1);
                images.splice(index, 1);
              }
              target.remove();
              gallery.hide();
            });
          }
        });
      });
    this.images = images;
  }
};
</script>

<style>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
html,
body {
  background-color: #f0eff5;
}
.wrap {
  background-color: #f0eff5;
  width: 100vw;
  height: 100vh;
}
.text {
  padding: 20px 0 0px 15px;
  color: #2c3e50;
}
.weui-cells__title {
  font-size: 16px;
}
.weui-uploader {
  padding: 10px 5px;
}
.submit {
  padding: 20px;
}
.footer {
  bottom: 0;
  left: 0;
  right: 0;
  padding-top: 40px;
  padding-bottom: 10px;
  text-align: center;
}
</style>



