<template>
  <div class='wrap'>
    <wv-group title="问题描述或意见反馈" style="margin-top: 20px;">
      <wv-textarea placeholder="请填写问题描述，以便我们提供更好的帮助和改进" :rows="6" :show-counter="false" v-model="feedback"></wv-textarea>
    </wv-group>
    <div class="weui-gallery">
      <span class="weui-gallery__img" style="background-image: url(./images/pic_article.png);"></span>
      <div class="weui-gallery__opr">
          <a href="javascript:" class="weui-gallery__del">
              <i class="weui-icon-delete weui-icon_gallery-delete"></i>
          </a>
      </div>
    </div>
    <div class="weui-cells weui-cells_form" id="uploaderCustom">
        <div class="weui-cell">
            <div class="weui-cell__bd">
                <div class="weui-uploader">
                    <div class="weui-uploader__hd">
                        <p class="weui-uploader__title">图片上传（填选，提供问题截图）</p>
                        <div class="weui-uploader__info"><span id="uploadCount">0</span>/{{maxCount}}</div>
                    </div>
                    <div class="weui-uploader__bd">
                        <ul class="weui-uploader__files" id="uploaderCustomFiles"></ul>
                        <div class="weui-uploader__input-box">
                            <input id="uploaderCustomInput" class="weui-uploader__input" type="file" accept="image/*" multiple="multiple">
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
    <wv-group title="联系电话">
      <wv-input placeholder="选填，便于我们与您联系" type="number" v-model="phoneNumber"></wv-input>
    </wv-group>
    <p class="submit"><wv-button id="uploaderCustomBtn" type="primary" @click="showSuccess">提交反馈</wv-button></p>
    <wv-footer class="footer" text="Copyright © 2018 xingnanzhuli"></wv-footer>
  </div>
</template>

<script>
import axios from "axios";
import { Toast } from "we-vue";
import weui from "weui.js";

export default {
  name: "Feedback",
  props: ["maxCount", "uuid", "images"], // 在App.vue中设置
  data() {
    return {
      text: "问题描述或意见反馈",
      feedback: "",
      phoneNumber: ""
    };
  },
  methods: {
    showSuccess() {
      if (this.feedback === "") {
        Toast.text({
          duration: 1000,
          message: "反馈内容不能为空"
        });
      } else {
        axios
          .post("/feedback", {
            params: {
              uuid: this.uuid,
              feedback: this.feedback,
              phoneNumber: this.phoneNumber,
              images: this.images
            }
          })
          .then(res => {
            let text = "恋爱联盟感谢您的关注与支持，我们会尽快认真处理您的反馈";
            this.$router.push({ name: "Reminder", params: { text } });
          });
      }
    }
  }
};
</script>

<style scoped>
#uploader {
  margin-top: 20px;
}
</style>



